<template>
  <div class="col-md-12 col-12">
    <h1 class="h1 mb-3">Ubah Password</h1>
    <div class="card">
      <div class="card-body">
        <form @submit.prevent="submit">
          <div class="form-group">
            <label for="npm">NPM</label>
            <input
              type="text"
              class="form-control"
              id="npm"
              disabled
              placeholder="NPM"
              :value="getDataProfile.npm"
            />
          </div>
          <div class="form-group">
            <label for="nama">Nama Lengkap</label>
            <input
              type="text"
              class="form-control"
              id="nama"
              disabled
              placeholder="Nama Lengkap"
              :value="getDataProfile.full_name"
            />
          </div>
          <div class="form-group">
            <label for="email">Email</label>
            <input
              type="text"
              class="form-control"
              id="email"
              disabled
              placeholder="Email"
              :value="getDataProfile.email_student"
            />
          </div>
          <div class="form-group">
            <label for="lama">Password Lama</label>
            <input
              type="password"
              class="form-control"
              id="lama"
              placeholder="Password Lama"
              v-model="form.password"
            />
          </div>
          <div class="form-group">
            <label for="baru">Password Baru</label>
            <input
              type="password"
              class="form-control"
              id="baru"
              placeholder="Password Baru, Minimal 6 karakter"
              v-model="form.new_password"
            />
          </div>
          <div class="form-group">
            <label for="ulangi">Ulangi Password Baru</label>
            <input
              type="password"
              class="form-control"
              id="ulangi"
              placeholder="Ulangi Password Baru"
              v-model="form.new_password_confirmation"
            />
          </div>
          <button type="submit" class="btn btn-primary btn-success btn-sm ml-0">
            Simpan
          </button>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
export default {
  name: "ubahpassword",
  inject: ["notyf"],
  data() {
    return {
      form: {
        password: null,
        new_password: null,
        new_password_confirmation: null,
      },
    };
  },
  computed: {
    ...mapGetters({
      getNPM: "auth/users",
      getDataProfile: "profile/profile",
    }),
  },
  methods: {
    ...mapActions({
      profile: "profile/profile",
    }),
    getProfile() {
      this.profile();
    },
    async submit() {
      try {
        let dataPass = {
          username: this.getNPM.username,
          password: this.form.password,
          new_password: this.form.new_password,
          new_password_confirmation: this.form.new_password_confirmation,
        };
        let response = await axios.post("/oasis/change-password", dataPass);
        console.log(dataPass);

        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }
        return response;
      } catch (error) {
        console.warn(error);
      }
    },
  },
  mounted() {
    this.getProfile();
    // this.form.password = this.getDaftar.password;
  },
};
</script>
